.ds-makersun-dozen-tabs-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}
.ds-makersun-dozen-tabs-container .ds-makersun-dozen-tabs-header-container {
  display: flex;
}
.ds-makersun-dozen-tabs-container .ds-makersun-dozen-tabs-content-container {
  display: flex;
  position: relative;
  width: 100%;
  transition: transform 0.3s ease-in-out;
}
.ds-makersun-dozen-tabs-container .ds-makersun-dozen-tabs-content-container > div {
  flex: 0 0 100%;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 2rem;
}